// app/news/page.tsx
"use client";

import Image from "next/image";
import Link from "next/link";
import React from "react";
import { NEWS } from "../data/data";

function generateSlug(title: string) {
  return title
    .toLowerCase()
    .replace(/[^a-z0-9]+/g, "-")
    .replace(/^-+|-+$/g, "");
}

export default function NewsPage() {
  return (
    <main className="w-full bg-white text-[#1e1e1e]">
      {/* Page container: responsive padding; keeps your 70/173 on desktop */}
      <section className="mx-auto w-full px-4 sm:px-6 md:px-8 lg:px-[70px] py-32 md:py-32 lg:py-[173px] ">
        {/* Heading */}
        <header className="mb-8 sm:mb-10 lg:mb-12 pb-3">
          <h1 className="font-editors  sm:font-medium text-[22px] italic font-semibold sm:not-italic sm:text-[26px] lg:text-[34px] leading-tight">
            Discover all the{" "}
            <span className="italic sm:font-medium font-semibold">
              latest news
            </span>
          </h1>
        </header>

        <div className="space-y-12 lg:space-y-14">
          {NEWS.map((n) => (
            <article
              key={n.id}
              className="grid grid-cols-1 xl:grid-cols-12 gap-6 lg:gap-8"
            >
              <div className="lg:col-span-5 w-full lg:w-[380px] lg:min-h-[390px]">
                <div className="text-[12px] text-black/60 mb-3">{n.date}</div>

                <h2 className="font-editors lg:w-[130%] text-[20px] sm:font-medium font-semibold sm:text-[22px] leading-snug">
                  {n.title}
                </h2>
                {n.subtitle && (
                  <h3 className="font-editors mb-2 sm:mb-0 italic sm:font-medium font-semibold text-[18px] sm:text-[22px] leading-snug">
                    {n.subtitle}
                  </h3>
                )}

                <div className="block sm:hidden my-4">
                  <div
                    className="
          relative w-full
          h-auto
          max-w-full
        "
                  >
                    <Image
                      src={n.image}
                      alt={n.title}
                      width={760}
                      height={390}
                      className="w-full h-auto object-cover"
                      priority
                    />
                  </div>
                </div>

                <div className="lg:my-[52px] my-[20px] md:my-[30px] h-px w-16 bg-black/40 sm:block hidden" />
                <p className="font-helvetica text-[14px] lg:w-[140%] leading-relaxed text-black/80">
                  {n.excerpt}
                </p>
                {n.excerpt2 && (
                  <p className="mt-6 font-helvetica text-[14px] leading-relaxed text-black/80">
                    {n.excerpt2}
                  </p>
                )}
                <div className="mt-6">
                  <Link
                    href={`/news/${generateSlug(n.title)}`}
                    className="h-[37px]  bg-[#e4dedd] text-black w-[145px] backdrop-blur-md flex items-center justify-center rounded-full font-helvetica text-[14px] hover:bg-[#d9cfcf] transition-colors"
                  >
                    Read More
                  </Link>
                </div>
              </div>

              {/* Right: image - desktop/tablet only */}
              <div className="xl:col-span-7 flex justify-center hidden sm:flex">
                <div
                  className="
        relative w-full
        h-auto
        lg:h-[390px]
        max-w-full lg:max-w-[760px]
      "
                >
                  <Image
                    src={n.image}
                    alt={n.title}
                    width={760}
                    height={390}
                    className="
          w-full h-auto
          object-cover lg:object-contain
        "
                    priority
                  />
                </div>
              </div>
            </article>
          ))}
        </div>
      </section>
    </main>
  );
}
